import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import mixins from './mixins'


const app = createApp(App);
app.use(router);
app.mixin(mixins);
app.use(store);
///pubilc index.html   app id  div  .
app.mount('#app');

window.Kakao.init("    ")