<template>
  <html>

  <body>
    <form id="app2" @submit="checkForm" action="/something" method="post" novalidate="true">
      <!--     -->
      <div class="contact-form-area" style="padding-top: 6%; height: 100px; margin: auto;">
        <div class="contact-text">
          <h1>  </h1>
          <div style="float: left; margin-top: 10%; margin-left: 20%; ">

            <div class="field2" id="sign">
              <b label for="name"> </b>
              <br />
              <input type="text" id="name" role="textbox" placeholder="  " name="name"
                v-model="name" /><br /><br />

            </div>

            <!-- 5. _ -->
            <div class="field2 tel-number">
              <b label for="phone">(-)</b>
              <div>
                <input id="phone1" type="text" role="textbox" name="phone" placeholder="ex)00012345678"
                  v-model="phone" /><br /><br />
              </div>
            </div>

            <div class="field2" id="sign">
              <b label for="email">()</b><br>
              <input id="email" type="text" role="textbox" placeholder="  ." name="email"
                v-model="email" /><br /><br />
            </div>

            <div class="field2" id="sign">
              <b> </b>&nbsp;&nbsp;
              <select v-model="capacity" class="capacity" style="width: 70px;">
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
              </select>
            </div>

            <p v-if="errors.length">
              <br>
              <b>  </b>
            <ul>
              <li v-for="error in errors" v-bind:key="error" style="color: red;">{{ error }}</li>
            </ul>
            </p>

            <div style="position: relative; left: 125px; top: 30px">
              <!-- <input type="submit" id="addallow" value="" @click="PaymentInsert"> -->
              <input type="submit" v-on:click="requestPay()" id="addallow" value="">

            </div>
          </div>
        </div>
      </div>

      <!-- -->
      <div class="right" style="margin-right: 15%;">
        <section class="info" data-v-33033856="">
          <p class="name" data-v-33033856="">
            <strong data-v-33033856=""></strong>
            <!--{{acco.name}}--> 
          </p>
          <p data-v-33033856=""><strong data-v-33033856=""></strong>06.29  15:00
          </p>
          <p data-v-33033856=""><strong data-v-33033856=""></strong>06.30  12:00
          </p>
        </section>
        <section class="total_price_pc" data-v-33033856="">
          <p data-v-33033856="">
            <strong data-v-33033856="">
              <b data-v-33033856="">  </b>(VAT)</strong>
            <span class="in_price" data-v-33033856=""><!--{{getCurrencyFormat(totalPrice)} * }-->85,000</span>
          </p>
          <ul data-v-33033856="">
            <li data-v-33033856="">  ,   </li>
            <li data-v-33033856="">
                <span data-v-33033856=""> </span> 
              <span data-v-33033856=""></span>  
            </li>
          </ul>
        </section> <!---->
      </div>
    </form>
  </body>

  </html>
</template>


<script>
var IMP = window.IMP;   //  
IMP.init("imp56476234");

export default {
  el: "#app2",
  data() {
    return {
      capacity: "2",
      errors: [],
      name: null,
      email: null,
      phone: null,
      total: 1,
      totalPrice: 0
    }
  },
  methods: {
    checkForm(e) {
      e.preventDefault();
      this.errors = [];
      if (!this.name) {
        this.errors.push(" .");
      }

      if (!this.phone) {
        this.errors.push(" ");
      } else if (!this.validPhone(this.phone)) {
        this.errors.push("  .");
      }

      if (!this.email) {
        this.errors.push(" .");
      } else if (!this.validEmail(this.email)) {
        this.errors.push("  .");
      }

      if (!this.errors.length) return true;
    },
    validEmail: function (email) {
      var re1 = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
      return re1.test(email);
    },

    validPhone: function (phone) {
      var re2 = /^01([0|1|6|7|8|9]?)?([0-9]{3,4})?([0-9]{4})$/
      return re2.test(phone);
    },

    PaymentInsert() {
      this.$swal.fire({
        title: '  ?',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: ''
      }).then(async (result) => {
        if (result.isConfirmed) {
          await this.$api("/api/PaymentInsert", { param: [this.payment] });
          this.swal.fire('!', '', 'success');
          this.$router.push({ path: '/payment' });
        }
      })
    },




    getCurrencyFormat(value) {
      return this.$currencyFormat(value);
    },


    requestPay() {
      if (this.name == null || this.phone == null || this.email === null || !this.validEmail(this.email)) {
        return
      }
      IMP.request_pay({ // param
        pg: "inicis",
        pay_method: "card",
        merchant_uid: "57008833-33004",
        name: " 10kg",
        amount: 1004,
        buyer_email: "Iamport@chai.finance",
        buyer_name: " ",
        buyer_tel: "010-1234-5678",
        buyer_addr: "  ",
        buyer_postcode: "123-456",
      }, rsp => { // callback
        if (rsp.success) {
          alert(" .");
          location.href = "/reservation_info"
          //    ,

        } else {
          alert(" ");
          //    ,

        }
      });
    }
  }
}

</script>

<style>
@import "Payment.css";

body,
h2,
p,
div,
strong,
span,
input,
a {
  text-align: left;
}

.error {
  font-size: 1px;
