<template>
  <div>
    <!-- ***** Contact Area Start ***** -->
    <div class="dorne-contact-area d-md-flex container" style="padding-top: 43px" id="contact">
      <!-- -->
      <div style="width: 20%">
        <div class="leftsidebar" style="width: 20%; padding-top: 30%">
          <button type="button" id="mypage_button" style="max-width: 20px" @click="Mypage_Link">
            
          </button>
          <button type="button" id="mypage_button" style="max-width: 20px; color: red" @click="My_reservation_Link">
            
          </button>
          <button type="button" id="mypage_button" style="max-width: 20px" @click="My_Delete_Link">
             
          </button>
        </div>
      </div>
      <!--     -->
      <div class="contact-form-area equal-height container" style="padding-top: 6%">
        <div class="contact-text">
          <h1></h1>
          <div v-if="res_acco == null" class="reslistempty">
            <div class="really2">
                <br />  
            </div>
            <br />
            <button type="button" id="main" @click="main_link">
                 
            </button>
          </div>
          <div v-else style="position: relative; left: 18%">
            <div class="res-content">
              <!--<img
                src="'/download/${ACCO_ID}/${ACCO_IMAGE}'"
                class="room-image"
              />-->
              <img class="lazy" src="//image.goodchoice.kr/resize_1000X500x0/affiliate/2023/06/16/648c25cfa5a22.jpg"
                style="display: block" />
              <strong style="font-size: 17px">{{ ACCO_NAME }}</strong>
              <div style="position: relative; left: 69px">
                <button id="button2" style="display: inline-block" @click="openModal()">
                  
                </button>
                <button id="button3" @click="showConfirmation()" style="display: inline-block">
                  
                </button>
              </div>
              <div id="popup" class="popup" style="display: none; left: 30px; top:50px;">
                <p class="popup-message" style="font-size: 18px">
                   ?
                </p>
                <div class="popup-buttons">
                  <button id="button7" @click="hideConfirmation()"></button>
                </div>
                <div class="popup-buttons">
                  <button id="button8" @click="cancelReservation()">
                    
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div id="modal" class="modal">
          <div class="modal-content">
            <div class="reslistpopup">
              <h2> </h2>
              <div>
                <strong>{{ ACCO_NAME }}</strong>
              </div>
              <section>
                <div>
                  <p><strong></strong> {{ RESERVATION_CHECK_IN }}</p>
                  <p><strong></strong> {{ RESERVATION_CHECK_OUT }}</p>
                </div>
                <div>
                  <p><strong></strong> {{ RESERVATION_ID }}</p>
                  <p><strong> </strong> {{ USER_ID }}</p>
                  <p><strong></strong> {{ RESERVATION_CAPACITY }}</p>
                </div>
              </section>
              <div class="total">
                <div class="payment-info">
                  <div class="payment-info-top">
                    <p><strong>  </strong></p>
                  </div>
                  <div class="payment-info-middle">
                    <p>
                      <b>{{ PAYMENT_TOTAL_PRICE }}</b>
                    </p>
                  </div>
                </div>
              </div>
              <a href="{{ RESERVATION_TEL }}" class="btn-call"></a>
              <section>
                <p>
                  <input type="button" value="" @click="closeModal()" />
                </p>
              </section>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      // user   
      ACCO_NAME: "",
      RESERVATION_CHECK_IN: "",
      RESERVATION_CHECK_OUT: "",
      PAYMENT_TOTAL_PRICE: "",
      RESERVATION_TEL: "",
      ACCO_IMAGE: "",
      res_acco: [],
      RESERVATION_CAPACITY: "",
    };
  },
  mounted() {
    //   
    this.Get_Reservation_Info();
  },

  methods: {
    Mypage_Link() {
      this.$router.push({ path: "/mypage" });
    },
    My_reservation_Link() {
      this.$router.push({ path: "/my_reservation" });
    },
    mypage_pw_check_Link() {
      this.$router.push({ path: "/mypage_pw_check" });
    },
    My_Delete_Link() {
      this.$router.push({ path: "/my_delete" });
    },
    main_link() {
      this.$router.push({ path: "/" });
    },

    //
    showConfirmation() {
      var popup = document.getElementById("popup");
      popup.style.display = "block";
    },
    hideConfirmation() {
      var popup = document.getElementById("popup");
      popup.style.display = "none";
    },
    cancelReservation() {
      alert(" ");
      var popup = document.getElementById("popup");
      popup.style.display = "none";
    },

    // 
    openModal() {
      var modal = document.getElementById("modal");
      modal.style.display = "block";
    },

    closeModal() {
      var modal = document.getElementById("modal");
      modal.style.display = "none";
    },

    //
    Get_Reservation_Info() {
      this.ACCO_NAME = "";
      this.RESERVATION_ID = "000001";
      this.USER_ID = "";
      this.RESERVATION_CHECK_IN = "2023-07-07";
      this.RESERVATION_CHECK_OUT = "2023-07-08";
      this.PAYMENT_TOTAL_PRICE = "100,000";
      this.RESERVATION_TEL = "010-1111-1111";
      this.RESERVATION_CAPACITY = "2";
    },
  },
};
</script>
<style>
@import "Mypage.css";
</style>