// Vuex            .       
// Store         Store  
import { createStore } from "vuex";

//       
//vuex state     
import persistedstate from "vuex-persistedstate";

const store = createStore({
  state() {
    //    . State   Component computed  .   : this.$store.state.items
    return {
      user: {},
    };
  },
  mutations: {
    //Mutation  State . . Mutations   Component Methods  .   : this.$store.commit('/')
    user(state, data) {
      state.user = data;
    },
  },
  plugins: [
    persistedstate({
      paths: ['user']
    })
  ]
});

export default store;